<template>
  <div class="material">
    <div class="mb-5 company-top">
      <Button type="success" class="float-right" ghost></Button>
      <Button type="success" class="float-right mr-10" ghost @click="toPreset"></Button>
    </div>
    <!--+-->
    <div class="company-info">
      <div class="row justify-content-between">
        <div class="float-left company-title">
          <strong class="company-name">{{companyName}}</strong>
        </div>
        <div class="float-right text-center company-operating">
          <a class="mr-10" @click="addBusiness"></a>
          <a v-if="practicalAnswer" class="mr-10"></a>
          <a v-else class="mr-10" @click="toPracticalAnswer"></a>
          <a class="mr-10"></a>
          <a class="mr-10"></a>
        </div>
      </div>
    </div>
    <div class="table-responsive">
      <Table border :columns="tableTitle" :data="tableData" size="large" style="width: 100%;">
        <template slot-scope="{ row }" slot="content">
          <strong>{{ row.content }}</strong>
        </template>
        <template slot-scope="{ row, index }" slot="action">
          <a style="margin-right: 5px" @click="show(index)"></a>
          <a @click="remove(index)"></a>
        </template>
      </Table>
    </div>
    <!---->
    <div class="mt-15 text-center">
      <Page :total="dataCount" size="small" :page-size="pageSize" class="paging" @on-change="changepage"></Page>
    </div>
    <!---->
    <div v-show="showPreseteadd">
      <div class="showPreseteadd">
        <Preseteadd></Preseteadd>
      </div>
    </div>
    <!---->
    <div v-show="showCompanyadd">
      <Popups>
        <div slot="title"></div>
        <div slot="close">
          <Icon type="md-close companyClose" @click="showCompanyaddoff"/>
        </div>
        <div slot="content">
          <div class="preset">
            <Tabs type="card">
              <TabPane label="">
                <!---->
                <div class="table-content">
                  <form class="companyInfo">
                    <Table border size="small" :columns="companyTitle" :data="companyData"></Table>
                  </form>
                  <Button type="success" class="mt-10 float-right" ghost @click="saveCompanyInfo"> </Button>
                </div>
              </TabPane>
              <TabPane label="">
                <!---->
                <div class="table-content">
                  <div class="d-flex">
                    <Button type="success" class="float-right" style="margin-bottom: 10px;" ghost @click="addStampDuty">
                       +
                    </Button>
                    <Divider/>
                  </div>
                  <div class="business-list" style="height: 80%; overflow: auto;">
                    <Row>
                      <Col span="6" v-for="item,index in stampList" :key="index" class="mr-5 mt-5"
                           style="min-width: 200px;">
                        <Card>
                          <p slot="title"> {{index}}</p>
                          <p>{{item.taxable_name}}</p>
                          <p>{{item.taxes_status}}</p>
                          <p>{{item.tax_rate}}</p>
                          <p>
                            <a class="mr-3"></a>
                            <a></a>
                          </p>
                        </Card>
                      </Col>
                    </Row>
                  </div>
                </div>
              </TabPane>
              <TabPane label="">
                <!---->
                <div class="table-content">
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-secondary">N</span>
                  </div>
                </div>
              </TabPane>
              <TabPane label="">
                <!---->
                <div class="table-content">
                  <Button title="" @click="openTable('p7')">
                    
                    <Icon v-if="tableStatus.p7" type="ios-star"/>
                    <Icon v-else type="ios-star-outline"/>
                  </Button>
                  <Button title="">
                    
                    <Icon type="ios-star-outline"/>
                    <Icon type="ios-star"/>
                  </Button>
                </div>
              </TabPane>
              <TabPane label="">
                <!---->
                <div class="table-content">
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="()">
                    <p>()</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                </div>
              </TabPane>
              <TabPane label="">
                <!---->
                <div class="table-content">
                  <div class="vat-button" title="A">
                    <p>A</p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                  <div class="vat-button" title="">
                    <p></p>
                    <span class="badge badge-success">Y</span>
                  </div>
                </div>
              </TabPane>
            </Tabs>
          </div>
        </div>
      </Popups>
    </div>
    <!---->
    <div v-show="showStamp">
      <div class="showStamp">
        <Stamp></Stamp>
      </div>
    </div>
    <!---->
    <div v-show="showTablePreset">
      <div v-if="tableCode == 'p7'" class="p7">
        <P7></P7>
      </div>
    </div>
  </div>
</template>

<script>
  // 
  import Preseteadd from './preset/presetadd'
  import common from '../mixins/common'
  import { Quill } from 'vue-quill-editor'
  import { ImageExtend, QuillWatch } from 'quill-image-extend-module'
  import Popups from './pop-ups-model' // 
  import Stamp from './preset/stampadd' // 
  // 
  import P7 from './preset/preset007'

  Quill.register('modules/ImageExtend', ImageExtend)
  export default {
    name: 'Material',
    mixins: [common],
    components: {
      Preseteadd,
      Popups,
      Stamp,
      P7 // 
    },
    data() {
      return {
        // 
        showPreseteadd: false,
        showCompanyadd: false,
        showTablePreset: false,
        // 
        practicalAnswer: false,
        // 
        tableTitle: [
          {
            title: '',
            key: 'content'
          },
          {
            title: '',
            key: 'course',
            width: 100,
            align: 'center'
          },
          {
            title: '',
            slot: 'action',
            width: 150,
            align: 'center'
          }
        ],
        tableData: [], // 
        // 
        ajaxHistoryData: [],
        // 
        dataCount: 0,
        // 
        pageSize: 1,
        // 
        page: 1,
        historyData: [], // 
        companyName: '', // 
        // 
        editorOption: {
          placeholder: '',
          modules: {
            ImageExtend: {
              loading: true,
              name: 'file',
              size: 1, // M, 1M = 1024KB
              // action: window.host + 'uploadEditorImg',
              action: '',
              response: (res) => {
                if (res.status === 1) {
                  return res.resultData
                } else {
                  this.$message.error(res.info)
                }
              },
              sizeError: () => {
                this.$message.error('1M')
              },
              //  
              headers: (xhr) => {
                xhr.setRequestHeader('token', this.$store.state.access_token_teacher)
              }
            },
            toolbar: {
              container: ['bold', 'italic', 'underline', { 'script': 'sub' }, { 'script': 'super' }, 'clean', 'image'],
              handlers: {
                'image': function () {
                  QuillWatch.emit(this.quill.id)
                }
              }
            }
          }
        },
        // 
        // 
        showStamp: false,
        stampList: [],
        //
        companyTitle: [
          {
            title: '',
            key: 'projectA',
            width: 115
          },
          {
            title: '',
            key: 'contentA',
            render: (h, params) => {
              return h('div', [
                h('Input', {
                  props: {
                    placeholder: this.companyPrompt[params.index][params.column._index].prompt,
                    name: this.companyPrompt[params.index][params.column._index].name,
                    value: this.companyPrompt[params.index][params.column._index].value
                  },
                  title: this.companyPrompt[params.index][params.column._index].value,
                  style: {
                    marginRight: '5px'
                  }
                }, 'View')
              ])
            }
          },
          {
            title: '',
            key: 'projectB',
            width: 115
          },
          {
            title: '',
            key: 'contentB',
            render: (h, params) => {
              return h('div', [
                h('Input', {
                  props: {
                    placeholder: this.companyPrompt[params.index][params.column._index].prompt,
                    name: this.companyPrompt[params.index][params.column._index].name,
                    value: this.companyPrompt[params.index][params.column._index].value
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.abc(params)
                    }
                  }
                }, 'View')
              ])
            }
          }
        ],
        companyData: [
          {
            projectA: '',
            projectB: ''
          },
          {
            projectA: '',
            projectB: ''
          },
          {
            projectA: '',
            projectB: ''
          },
          {
            projectA: '',
            projectB: ''
          },
          {
            projectA: '',
            projectB: ''
          },
          {
            projectA: '',
            projectB: ''
          },
          {
            projectA: '',
            projectB: ''
          }
        ],
        // 
        companyPrompt: {
          0: {
            1: {
              prompt: '12 (0)',
              name: 'trade_id',
              value: ''
            },
            3: {
              prompt: '',
              name: 'firm_name',
              value: ''
            }
          },
          1: {
            1: {
              prompt: '91370305755666003U',
              name: 'firm_code',
              value: '91370305755666003U'
            },
            3: {
              prompt: '',
              name: 'firm_trade',
              value: ''
            }
          },
          2: {
            1: {
              prompt: '',
              name: 'firm_detail_trade',
              value: ''
            },
            3: {
              prompt: '',
              name: 'firm_registration_trade',
              value: ''
            }
          },
          3: {
            1: {
              prompt: '',
              name: 'firm_legal_person',
              value: ''
            },
            3: {
              prompt: '',
              name: 'firm_address',
              value: ''
            }
          },
          4: {
            1: {
              prompt: '',
              name: 'firm_operating',
              value: ''
            },
            3: {
              prompt: '5159889499889556',
              name: 'firm_bank_code',
              value: '5159889499889556'
            }
          },
          5: {
            1: {
              prompt: '',
              name: 'firm_type',
              value: ''
            },
            3: {
              prompt: '15275918888',
              name: 'firm_telephone',
              value: '15275918888'
            }
          },
          6: {
            1: {
              prompt: '',
              name: 'telephone_name',
              value: ''
            },
            3: {
              prompt: '',
              name: 'taxation_name',
              value: ''
            }
          }
        },
        // 
        tableStatus: {
          p7: false
        },
        tableCode: '' //
      }
    },
    created() {
      this.handleListApproveHistory()
    },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill
      }
    },
    methods: {
      // 
      toPreset() {
        this.showCompanyadd = true
      },
      showCompanyaddoff() {
        this.showCompanyadd = false
      },
      // 
      toPracticalAnswer() {
        this.practicalAnswer = true
      },
      // 
      addBusiness() {
        this.showPreseteadd = true
        layui.use('layer', () => {
          var layer = layui.layer
          setTimeout(() => {
            var index = layer.open({
              type: 1,
              title: '',
              shadeClose: false,
              shade: 0.2,
              area: ['500px', '510px'],
              content: J('.businessAdd'), //iframeurl
              end: () => {
                this.showPreseteadd = false
              }
            })
          }, 5)
        })
      },
      // 
      saveBusinessInfo() {
        const trade_id = this.$store.state.preset.trade_id
        var preset = this.preset
        this.$store.commit('saveBusiness', { name: trade_id, data: preset })
        if (this.$store.state.preset.companyInfo.business[trade_id]) {
          this.success()
          layer.close(layer.index) //
        }
      },
      show(index) {
        this.$Modal.info({
          title: '',
          content: `${this.tableData[index].content}<br>${this.tableData[index].course}<br>${this.tableData[index].textAnalysis}<br>${this.tableData[index].videoAnalysis}`
        })
      },
      remove(index) {
        this.tableData.splice(index, 1)
      },
      // 
      // 
      handleListApproveHistory() {
        // 
        this.ajaxHistoryData = Object.keys(this.$store.state.preset.companyInfo.company)

        this.dataCount = Object.keys(this.$store.state.preset.companyInfo.company).length
        // 
        if (Object.keys(this.$store.state.preset.companyInfo.company).length < this.pageSize) {
          this.historyData = this.ajaxHistoryData
        } else {
          const stateCompany = this.$store.state.preset.companyInfo.company
          this.historyData = this.ajaxHistoryData.slice(0, this.pageSize)
          // 
          this.companyName = stateCompany[this.historyData][1].value
          //
          this.$store.commit('saveTradeid', this.historyData)
          // 
          this.tableDataInfo()
          //
          this.stampList = this.$store.state.preset.companyInfo.preset[this.historyData]['YHS']
        }
      },
      changepage(index) {
        // 
        this.page = index
        let _start = (index - 1) * this.pageSize
        let _end = index * this.pageSize
        this.historyData = this.ajaxHistoryData.slice(_start, _end)
        // 
        const stateCompany = this.$store.state.preset.companyInfo.company
        this.companyName = stateCompany[this.historyData][1].value
        // 
        this.$store.commit('saveTradeid', this.historyData)
        this.tableDataInfo()
      },
      tableDataInfo() {
        this.tableData = this.$store.state.preset.companyInfo.business[this.historyData]
      },
      // 
      addStampDuty() {
        this.showStamp = true
        layui.use('layer', () => {
          var layer = layui.layer
          setTimeout(() => {
            var index = layer.open({
              type: 1,
              title: '',
              shadeClose: false,
              shade: 0,
              area: ['300px', '290px'],
              content: J('.showStamp'),
              end: () => {
                this.showStamp = false
                this.stampList = this.$store.state.preset.companyInfo.preset[this.historyData]['YHS']
              }
            })
          }, 5)
        })
      },
      // 
      openTable(tableCode) {
        this.showTablePreset = true
        this.tableCode = tableCode
        layui.use('layer', () => {
          var layer = layui.layer
          setTimeout(() => {
            var index = layer.open({
              type: 1,
              title: '',
              shadeClose: false,
              shade: 0,
              area: ['95%', '95%'],
              content: J('.' + tableCode),
              end: () => {
                this.showTablePreset = false
              }
            })
          }, 5)
        })
      }
    }
  }
</script>

<style scoped>
  /**/
  .businessAdd {
    width: 100%;
    overflow: auto;
    padding: 10px;
  }

  .material {
    width: 100%;
    height: 100%;
    padding: 20px;
    overflow: auto;
  }

  .company-top {
    height: 35px;
  }

  .company-name {
    font-size: 14px;
    color: #515a6e;
  }

  .company-info {
    height: 40px;
    line-height: 30px;
    border: 1px solid #dee2e6;
    border-bottom: 0px;
  }

  .company-info .justify-content-between {
    padding-left: 15px;
    height: 100%;
  }

  .company-info .company-title {
    line-height: 40px;
    margin-left: 3px;
  }

  .company-info .company-operating {
    width: 250px;
    line-height: 40px;
    float: right;
  }

  a {
    color: #40ADDC !important;
    font-size: 13px;
    font-weight: 400;
  }

  .company-top button {
    color: #7CCB40;
    border-color: #7CCB40;
    width: 86px;
    height: 30px;
  }

  .company-top button:hover,
  .company-top button:active {
    color: #7CCB40 !important;
    background-color: #ffffff !important;
    border-color: #7CCB40 !important;
  }

  .companyClose {
    font-size: 20px;
    color: #65646E;
  }

  .companyClose:hover {
    color: #919098;
  }

  /**/
  .preset {
    width: 100%;
    height: 100%;
    padding: 20px;
  }

  .preset .table th {
    vertical-align: inherit !important;
  }

  .preset .table-content {
    height: 370px;
  }


</style>



