<template>
  <div class="table-content">
    <Form :model="StampItem" :label-width="80">
      <FormItem label="">
        <Input v-model="StampItem.taxable_name" placeholder=""></Input>
      </FormItem>
      <FormItem label="">
        <Select v-model="StampItem.taxes_status">
          <Option value=""></Option>
          <Option value=""></Option>
        </Select>
      </FormItem>
      <FormItem label="">
        <Input v-model="StampItem.tax_rate" placeholder="0.00003"></Input>
      </FormItem>
      <Button type="success" ghost class="float-right" @click="addstamp"></Button>
    </Form>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    name: 'Stampadd',
    mixins: [common],
    data() {
      return {
        StampItem: {
          taxable_name: '',
          taxes_status: '',
          tax_rate: ''
        }
      }
    },
    methods: {
      addstamp() {
        this.$store.commit('savePreset', { name: 'YHS', data: this.StampItem })
        this.success()
        setTimeout(() => {
          layer.close(layer.index) //layer
          Object.assign(this.$data, this.$options.data())
        }, 1500)
      }
    }
  }
</script>

<style scoped>
  .table-content {
    border-radius: 4px;
    padding: 10px;
    height: 100%;
    z-index: 0;
  }

  .stampSelect {
    width: 100%;
    height: 32px;
    box-sizing: border-box;
    vertical-align: middle;
    color: #515a6e;
    font-size: 14px;
    line-height: normal;
    border-radius: 4px;
    border: 1px solid #dcdee2;
    cursor: pointer;
  }
</style>



